	module.exports = {"data":{"markdownRemark":{"html":"<p>Puoi averla con te da accendere sul tuo altare, nella tua casa, nel luogo di lavoro o dove preferisci.</p>\n<p>Chiunque, ovunque viva, può mantenere in casa la Fiamma della Grande Madre delle Dolomiti, da accendere in qualsiasi occasione.<br>\nNon ci sono impegni di accensione per coloro che la detengono liberamente, poiché l'impegno a mantenerla perpetua è stato assunto dal gruppo delle Guardiane e Guardiani.<br>\nFare in modo che arda in molte case, nei luoghi di lavoro, nei centri di ricerca spirituale e non, durante i seminari, ecc. è ovviamente un dono anche per noi madrine e padrini di questo progetto.</p>\n<p>Spesso distribuiamo la Fiamma ai nostri eventi.<br>\nSe però vivi lontano, spediamo la Fiamma ovunque ci venga richiesta, chiedendo piccoli contributi così calcolati: rimborso spese materiali + una piccola offerta per auto sostenerci + spese di spedizione (nel caso delle scatoline sono 3 euro più spedizione).  </p>\n<br/>\n<p>\n  <img style='width:50%;' border=\"0\" alt=\"testo sopra\" src=\"/media/photos/tealight-fiamma.jpg\">\n</p>\n<br/>\n<p>Il modo più semplice per ottenerla è ordinare la scatolina di base, fatta a mano con amore, e che contiene una tea-light accesa dalla Fiamma custodita da uno dei guardiani.   L'energia della Fiamma risiede sulla parte dello stoppino che con lei è venuto in contatto, quindi si rianimerà ogni qual volta verrà riaccesa da voi.<br>\nSuggeriamo, una volta ottenuta la tea-light, di \"passare\" la Fiamma a candele e ceri più grandi e duraturi, per tutte le volte che vorrete!</p>\n<p>Per autosostenerci vendiamo anche delle candele più grandi, bicolore verde/azzurro e fatte per noi con metodi artigianali, al costo di 7 euro più spedizione. È possibile ottenere la Fiamma anche tramite queste candele.</p>\n<p>Per ulteriori informazioni <a href=\"/contatti\"><strong>contattaci</strong></a></p>","frontmatter":{"path":"/come-avere-la-fiamma","title":"Come avere la Fiamma"}}},"pathContext":{}}

